#navshow {
  display: none; }

.shadow {
  background-color: rgba(0, 0, 0, 0.5);
  width: 100vw;
  height: 100vh;
  z-index: 9;
  position: fixed;
  top: 0;
  left: 0;
  opacity: 0;
  visibility: hidden;
  transition: all 0.4s, transform 0.4s; }

@media screen and (max-width: 768px) {
  #navshow:checked ~ header nav {
    transform: translateX(100px);
    transition: all 0.4s, transform 0.4s; }
  #navshow:checked ~ header .bar:nth-of-type(1) {
    transform: translateY(8px) rotate(45deg);
    transition: all 0.4s, transform 0.4s;
    background-color: #fff; }
  #navshow:checked ~ header .bar:nth-of-type(2) {
    transform: translateX(100px);
    transition: all 0.4s, transform 0.4s;
    background-color: #fff; }
  #navshow:checked ~ header .bar:nth-of-type(3) {
    transform: translateY(-8px) rotate(-45deg);
    transition: all 0.4s, transform 0.4s;
    background-color: #fff; }
  #navshow:checked ~ footer,
  #navshow:checked ~ header,
  #navshow:checked ~ main {
    transform: translateX(-100px);
    transition: all 0.4s, transform 0.4s; }
  #navshow:checked ~ header .shadow {
    opacity: 1;
    visibility: visible;
    transition: all 0.4s, transform 0.4s; } }

